/* ==========================================================================
   #RESPONSIVE-RATIO
   ========================================================================== */

/*

Example : http://codepen.io/paallaire/pen/zNVwdR?editors=1100

div { @include responsive-ratio(16,9); }

*/

@mixin responsive-ratio($x, $y, $pseudo: false) {
  $padding: unquote( ( $y / $x) * 100 + '%');
  
  @if $pseudo {
    &:before {
      @include pseudo($pos: relative);
      width: 100%;
      padding-top: $padding;
    }
  }
  @else {
    padding-top: $padding;
  }
  
}


